  .navBarW {
    display: block;
    justify-content: space-around;
    height: 60px;
    width: 100%;
    padding-top: 5px;
  }
  .navBar_menusW {
    display: flex;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    margin: auto;
    align-items: center;
  }
  .navBar_menus_menuW a {
    color: #d6d6d6;
    text-decoration: none;
  }
  @media screen and (max-width: 768px) {
    .navBarW {
      justify-content: space-between;
      display: none;

    }
  }
  