.navBar {
    display: none;
  }
  .navBar_menus {
    display: block;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    margin: auto;
  }
  .navBar_menus_menu {
    color: #d6d6d6;
    text-decoration: none;
    display: block;
    margin: 0 auto;
    display: flex;
    width: 100%;
    flex-flow: column wrap;
  }
  @media screen and (max-width: 768px) {
    .navBar {
      justify-content: space-between;
      display: block;
      background: linear-gradient(to top, #001A46, #005495);
    }
    .navBar_menus {
      list-style: none;
      justify-content: space-around;
      width: 10%;
      height: 230px;
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-flow: row wrap;
    
    }
    .navBar_menus_menu {
      color: #d6d6d6;
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      display: flex;
      height: 35px;
      width: 300px;
      flex-flow: column wrap;
    }
  }
  